.wrapper{
    background-color: blue;
    width :100%;
    height: 100vh;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Fcontainer{
    width: 500px;
    background-color: #fff;
    padding: 30px;
    border-radius: 16px;
    background-color: rgba(0,0,0,.08) 0px 4px 12px;
}
.title-section{
    margin-bottom :30px;
}
.title{
    color:#38475a;
    font-size: 25px;
    font-weight: 500;
    text-transform: capitalize;
    margin-bottom: 10px;
}
.para{
    color :#38475a;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 20px;
    text-transform: capitalize;
}
.input-group{
    position: relative;
}
.input-group .label-title{
    color: #38475a;
    text-transform: capitalize;
    margin-bottom: 11px;
    font-size: 14px;
    display: block;
    font-weight: 500;
}
.input-group input{
    width: 100%;
    background-color: none;
    color: #38475a;
    height:50px;
    font-size: 16px;
    font-weight: 300;
    border:1px solid #EAECF0;
   padding: 9px 18px 9px 52px;
   outline: none;
   border-radius: 8px;
   margin-bottom: 20px;
}

.input-group input ::placeholder{
    color :#38475a;
    font-size: 16px;
    font-weight: 400;
}
.input-group .icon{
    position: absolute;
    color:#38475a;
    left:13px;
    top : calc(50% - 11px);
    text-align :center;
    font-size: 23px;

}

.submit-btn{
    width:100%;
    background-color: #106fde;
    border: 1px solid transparent;
    border-radius :8px;
    font-size: 16px;
    color: #fff;
    padding: 13px 24px;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    cursor: pointer ;
}

.submit-btn :hover{
    opacity: 0.95;
}
